



































import Vue from 'vue';
import { mapState } from 'vuex';

export default Vue.extend({
	data() {
		return {
			page: 1,
			pageCount: 0,
			itemsPerPage: 10,
			loading: false,
			headers: [
				{
					text: 'No',
					sortable: false,
					value: 'id',
					width: '100px',
					align: 'center',
				},
				{ text: '제목', value: 'title', sortable: false },
				{
					text: '글쓴이',
					value: 'username',
					sortable: false,
					width: '13%',
					align: 'center',
				},
				{
					text: '작성일',
					value: 'created_time',
					width: '200px',
					align: 'center',
				},
				{
					text: '조회수',
					value: 'views',
					width: '100px',
					align: 'center',
				},
			],
		};
	},

	computed: {
		...mapState(['posts']),
	},

	methods: {
		onClick(post) {
			this.$router.push(`/board/post/${post.id}`);
		},
	},
});
